import React, { Fragment } from 'react'
import asyncComponent from '../asyncComponent'
import './Works.css'

const TopBar = asyncComponent(() => import('../TopBar/TopBar'))
const skills = [
  {
    office: 'Illiyin Studio: Afrikgo Project',
    position: 'Frontend Developer',
    duration: '27 Agustus 2017 - 1 September 2017'
  },
  {
    office: 'DOT Indonesia: Dishub Project',
    position: 'Frontend Developer',
    duration: '15 Agustus 2017 - 18 September 2017'
  },
  {
    office: 'PT. Hash Rekayasa Teknologi',
    position: 'Web Developer',
    duration: '28 Februari 2018 - now'
  }
].map((el, idx) => (
  <div className="single-job row py-4" key={idx}>
    <div className="col-2">
      <i className="fa fa-briefcase icon" />
    </div>
    <div className="col-10">
      <h4 className="title">{el.office}</h4>
      <p className="desc">{el.position}</p>
      <p className="desc">{el.duration}</p>
    </div>
  </div>
))

export default () => {
  return (
    <Fragment>
      <TopBar bgColor="#744F81" color="white" icon="fa-briefcase">
        Work Experience
      </TopBar>
      <div className="item-container">
        <div className="container">{skills}</div>
      </div>
    </Fragment>
  )
}



